@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

/* base styles */
* {
	margin: 0;
	font-family: 'Quicksand';
	color: #333;
}
.navbar {
	padding: 20px;
	display: flex;
	align-items: center;
	max-width: 1000px;
	margin: 0 auto;
	border-bottom: 1px solid #f2f2f2;
}
.navbar h1 {
	color: rgb(42, 131, 247);
	font-size: 50px;
}
.navbar .links {
	margin-left: auto;
	font-size: 30px;
}
.navbar a {
	margin-left: 16px;
	text-decoration: none;
	padding: 6px;
}
.navbar a:hover {
	color: rgb(42, 131, 247);
}

.App {
	background-image: linear-gradient(#22ebd8, #4959c7);
	overflow: hidden;
	height: 1000px;
}
.wrapper div {
	height: 60px;
	width: 60px;
	border-radius: 50px;
	position: absolute;
	top: 10%;
	left: 10%;
	animation: 4s linear infinite;
	border: 2px solid(rgba(255, 255, 255, 0.7));
}
div .dot {
	height: 10px;
	width: 10px;
	border-radius: 50px;
	position: absolute;
	top: 20%;
	right: 20%;
	background: rgba(255, 255, 255, 0.5);
}
.wrapper div:nth-child(1) {
	top: 20%;
	right: 20%;
	animation: 8s linear infinite;
}
.wrapper div:nth-child(2) {
	top: 60%;
	right: 80%;
	animation: 10s linear infinite;
}
.wrapper div:nth-child(3) {
	top: 40%;
	right: 40%;
	animation: 3s linear infinite;
}
.wrapper div:nth-child(4) {
	top: 66%;
	right: 30%;
	animation: 7s linear infinite;
}
.wrapper div:nth-child(5) {
	top: 90%;
	right: 10%;
	animation: 9s linear infinite;
}
.wrapper div:nth-child(6) {
	top: 30%;
	right: 60%;
	animation: 6s linear infinite;
}
.wrapper div:nth-child(7) {
	top: 75%;
	right: 60%;
	animation: 10s linear infinite;
}
.wrapper div:nth-child(8) {
	top: 30%;
	right: 60%;
	animation: 5s linear infinite;
}
@keyframes animate {
	0% {
		transform: scale(0) translateY(0) rotate(70deg);
	}
	100% {
		transform: scale(1.3) translateY(-100px) rotate(360deg);
	}
}
.fish {
	position: absolute;
	--r: calc(var(--size) * 1vw);
	width: calc(var(--r) + var(--r) / 3);
	height: calc(var(--r) * 2);
	right: calc((var(--r) + var(--r) / 3) * -1);
	top: calc((--top) * 1px - var(--r));
	color: hsl(var(--color), 50%, 50%);
	transform-origin: left;
	animation: flow calc(var(--duration) * 1s) linear forwards,
		shake0.3s linear infinite;
}
@keyframes flow {
	to {
		right: 100%;
	}
}
@keyframes shake {
	50% {
		transform: rotateY(-35deg);
	}
	100% {
		transform: rotateY(35deg);
	}
}
.fish .body {
	position: absolute;
	border: var(--r) solid transparent;
	border-right-color: currentColor;
	border-left-style: none;
}
.fish .eye {
	position: absolute;
	--r1: calc(var(--r) / 4);
	width: var(--r1);
	height: var(--r1);
	background-color: #2e2e2e;
	top: 35%;
	left: 30%;
	border-radius: 50%;
}
.fish .fin {
	position: absolute;
	--r2: calc(var (--r) / 2);
	border-bottom: var(--r2) solid;
	border-left: var(--r2) solid trasform;
	filter: brightness(2.5);
	left: calc(var(--r) - var(--r2));
}
.fish .tail {
	position: absolute;
	--r3: calc(var(--r) / 3);
	border: var(--r3) solid transparent;
	border-right-color: currentColor;
	border-left-style: none;
	right: 0;
	top: calc(var(--r) - var(--r3));
}
.ripple {
	position: absolute;
	width: 1px;
	height: 1px;
	background: #ffffff80;
	border-radius: 50%;
	filter: opacity(0);
	left: calc(var(--left) * 1px);
	top: calc(var(--top) * 1px);
	animation: rippling 3s ease-out;
}
@keyframes rippling {
	form {
		transform: scale(0);
		filter: opacity(1);
	}
	to {
		transform: scale(100);
		filter: opacity(0);
	}
}

.content {
	max-width: 900px;
	max-height: 1000px;
	margin: 20px auto;
	padding: 20px;
	background-image: linear-gradient(#a5eee7, #d1d2d9);
	overflow: hidden;
}

/* blog preview / list */
.blog-preview {
	padding: 10px 16px;
	margin: 20px 0;
	border-bottom: 1px solid #f2f2f2;
}
.blog-preview:hover {
	box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.1);
}
.blog-preview h2 {
	font-size: 30px;
	color: rgb(42, 131, 247);
	margin-bottom: 8px;
}
.blog-preview li {
	font-size: 20px;
	color: rgb(2, 12, 25);
	margin-bottom: 8px;
}
.blog-preview a {
	text-decoration: none;
}

/* blog details page */
.blog-details h2 {
	font-size: 30px;
	color: rgb(42, 131, 247);
	margin-bottom: 10px;
}
.blog-details div {
	margin: 20px 0;
}
.blog-details button {
	background: rgb(42, 131, 247);
	color: #fff;
	font-size: medium;
	font-weight: bold;
	border: 0;
	padding: 15px;
	border-radius: 15px;
	cursor: pointer;
}

/* create new blog form */
.create {
	max-width: 400px;
	margin: 0 auto;
	text-align: center;
}
.create label {
	text-align: left;
	display: block;
}
.create h2 {
	font-size: 30px;
	color: rgb(42, 131, 247);
	margin-bottom: 30px;
}
.create input,
.create textarea,
.create select {
	width: 100%;
	padding: 6px 10px;
	margin: 10px 0;
	border: 1px solid #ddd;
	box-sizing: border-box;
	display: block;
}
.create button {
	background: rgb(42, 131, 247);
	color: #fff;
	border: 0;
	padding: 8px;
	border-radius: 8px;
	cursor: pointer;
}

.not-found a {
	text-decoration: none;
}
